.header img {
    width: 50px;
    height: 50px;
    padding-top: 5px;
    margin-left: 20px;
  }

  .header {
    grid-area: h;
    background-color: darkorange;
  }

  .login {
      float: right;
      padding-right: 20px;
      padding-top: 10px;
  }

  .login button {
      background-color: darkorange;
      border: none;
      color: white;
      cursor: pointer;
  }

  .login a {
      color: white;
      text-decoration: none;
  }

    .login a:hover {
        color: deepskyblue;

    }

    .summaryError {
        color: red;
        border: solid red 1px;
        display: block;
        width: 170px;
        text-align: center;
        font-size: 15px;
    }
